#Overcover{

  z-index: 900;
  height: 100vh;
  width: 100vw;
  position: absolute;
  background-color: rgba(20, 20, 20, 0.658);

  animation-name: fade;
  animation-direction: 800ms;
  -webkit-animation-fill-mode: both;
  -webkit-animation-duration: 0.8s; 
  animation-duration: 0.8s; 
  animation-fill-mode: both;
}@keyframes fade {
  0% {  z-index: 200;opacity: 1;background-color: rgba(25, 25, 25); }
  40% {  z-index: 200;opacity: 1;background-color: rgb(25, 25, 25); }
  60% {  z-index: 200;opacity: 1;background-color: rgb(25, 25, 25); }
  95% {  z-index: 200;opacity: 0;background-color: rgb(20, 20, 20, 0.658); }
  100% {  z-index: 0;opacity: 0;background-color: rgb(25, 25, 25); }
}


